.scroll-hint {
  position: relative;
}
.scroll-hint::after {
  content: "→";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  padding: 0.5rem;
  background: linear-gradient(to right, transparent, var(--v-black));
  pointer-events: none;
  display: none;
}
@media (max-width: 600px) {
  .scroll-hint.last-order__body::after, .scroll-hint.order-tracking__row--steps::after {
    display: block;
  }
}

@media (hover: none) and (pointer: coarse) {
